import React from 'react'; import styled from 'styled-components';
// ---------------------
const AboutMeNavContent = () => {
    return (
        <AboutContent>
            <List>
                <li style={{borderBottom: '1px solid #fff'}}><ThemeHeader>Содержание</ThemeHeader></li>
                <li style={{width: '95%', padding: '10px 0'}}>
                    <SubMenu>
                        <li style={{width: '100%'}}>
                            <ul >
                                <SubItem>Музыка</SubItem>
                                <SubItem>Игры</SubItem>
                                <SubItem>Спорт</SubItem>
                            </ul>
                        </li>
                        <li style={{width: '100%'}}>
                            <ul>
                                <SubItem>Психология</SubItem>
                                <SubItem>Волнения</SubItem>
                                <SubItem>Желания</SubItem>
                            </ul>
                        </li>
                    </SubMenu>
                </li>
            </List>
        </AboutContent>
    )
}

const AboutContent = styled.div `width: 100%; position: relative; 
padding: 10px 0 25px; margin: 10px 0;
color: #fff; background: rgba(0,0,0,.4); `
// -------------------------------
const     List     = styled.ul  `display: flex; flex-direction: column; align-items: center; width: 100%;`
const ThemeHeader  = styled.h2  `font-size: 200%; font-family: 'Architects Daughter', cursive; 
    @media (max-width: 820px) { }`
// ---------------------------------
const SubMenu = styled.ul `display: flex; justify-content: space-around;`
const SubItem = styled.li `margin: 0 4px; font-size: 180%; text-align: center; font-family: 'Architects Daughter', cursive;
    @media (max-width: 820px) { font-size: 140% }`

export default AboutMeNavContent