import React from 'react'; import styled from 'styled-components';
// ---------------------------------------------------------
import GameBackground from '~PREF/Img/Games.jpg'; 
const Game = () => (
    <Wrapper>
        <Bcg />
        <Header>Игры</Header>
        <FlexItem>
            <Paragraph>С малых лет я полюбил компьютерные игры, в 6 лет мне подарили SonyPlaystation, и я проводил с ней всё свое свободное время.</Paragraph>
            <Paragraph>Позже появился компьютер, и большую часть подросткового времени проводил именно за ним.</Paragraph>
        </FlexItem>  
        <FlexItem>
            <Paragraph>Так же меня интересовал кибер спорт, я играл в CSGO и смог получить самое большое звание в игре</Paragraph>
            <Paragraph>Хотел собрать свою комманду, или найти уже существующую для соревновательных игр с другими людьми</Paragraph>
            <Paragraph>Но жизнь не позволяла мне постоянно сидеть за компьютером и тренировать свои навыки игры</Paragraph>
            <Paragraph>Поэтому я перестал тратить на это много времени, и играл иногда в удовольствие :)</Paragraph>
        </FlexItem>
    </Wrapper>
)

const Wrapper = styled.div `width: 100%; height: 90vh; margin: 10px 0; padding: 0px 40px; position: relative;
    margin-top: 5px; border-top: 8px double rgba(255,255,255, .7); z-index: 1; 
    display: flex; justify-content: space-around; flex-direction: column;
    @media (max-width: 600px)  { padding: 5px; }`
const Bcg     = styled.div `width: 100%; height: 100%; position: absolute; top: 10px; left: 0%; z-index: -1; opacity: .4;   
    background: center center url(${GameBackground}); padding: 0 10px;`
    // ------------------------------------------------------------------
const FlexItem = styled.div `width: 100%; padding: 10px; margin: 5px 0; border-top: 1px solid rgba(255,255,255,.3); border-bottom: 1px solid rgba(255,255,255,.3); 
border-radius: 10px; box-shadow: inset 0 0 200px 10px #000, inset 0 0 30px 10px #000; text-shadow: 0 8px 3px #000;
    @media (max-width: 600px)  { padding: 0px 10px }`
const Header  = styled.h2  `font-size: 300%; text-align: center; color: #fff; margin-top: 20px; text-shadow: 0 1px 30px #fff;
    @media (max-width: 1030px) { font-size: 180% }
    @media (max-width: 700px)  { font-size: 150% }`
const Paragraph = styled.p ` font-family: 'Architects Daughter', cursive; font-size: 130%; text-align: center; color #fff; padding: 8px 5px;
    &:last-of-type { padding-bottom: 5px; }
    @media (max-width: 1030px) { font-size: 120% }
    @media (max-width: 760px)  { padding: 0px; }
    @media (max-width: 540px)  { font-size: 90%; }
    `



export default Game