import React from 'react'; import styled from 'styled-components';
// ---------------------------------------------------------------
import GreetingPhoto from '~PREF/img/Cyberial.jpg';

const Greting = () => (
        <GretingWR>
            <ImgWR> <img style={{borderRadius: '60px', height: '100%'}} src={GreetingPhoto}></img> </ImgWR> 
            <HeadingWR>
                    <Heading>Хей! Я Владик :) Будем знакомиться ?</Heading>
                    <AboutMeParagparh>А еще я (Front-End-Developher) Вроде бы :))</AboutMeParagparh>
                    <AboutMeParagparh>Эта страница созданна для ознакомления личных интересов автора.</AboutMeParagparh>
            </HeadingWR>
        </GretingWR>
    )
const GretingWR = styled.div `padding: 40px 40px; text-align: center; letter-spacing: 2px;
    display: flex; justify-content: space-between; align-items: center; border-bottom: 8px double rgba(255,255,255, .4);
        @media (max-width: 820px) { font-size: 80%; padding: 30px 15px; }
        @media (max-width: 700px) { flex-direction: column; padding: 10px; }
        @media (max-width: 500px) { font-size: 65%; letter-spacing: 1px; }`
// -----------------------------------------------------------
    const ImgWR  = styled.div `height: 240px; padding: 10px; border-radius: 10px; box-shadow: inset 0 0 40px 0 #fff;
    @media (max-width: 700px) { height: 150px; margin-bottom: 5px;}`
// -------------------------------------------------------------------------------------------------------------
const HeadingWR = styled.div `display: flex; width: 80%; align-items: center; justify-content: space-evenly; flex-direction: column;
        padding: 15px 0 25px 0;
    margin-left: 5px; border-radius: 30px; color: #fff; font-size: 100%; box-shadow: inset 0 0 200px 0 #000, 0 0 30px 0 #000;
    @media (max-width: 820px) { padding: 15px 20px 30px; }
    @media (max-width: 700px) { width: 100%; }
    @media (max-width: 500px) { padding: 10px; }`
// ---------------------------------------
const Heading =          styled.h2 `font-size: 200%; border-bottom: 1px double rgba(0,0,0,.2);`
const AboutMeParagparh = styled.p `font-size: 200%; padding: 10px 0; font-weight: 400; border-bottom: 1px double rgba(0,0,0,.2);`
// =================================================


    
export default Greting



